import React from "react";
import styles from "./Homepage.module.css";
import Typed from 'react-typed';  
import { Box, Grid, Image } from "@chakra-ui/react";
const Profilebanner = () => {
  return (
    <div className={`${styles.profile_banner}`} data-aos="fade-up" data-aos-duration="700">
      <div className={`${styles.profile_banner_image}`} data-aos="flip-right" data-aos-duration="700" >
        <img class="home-img"
          src="https://avatars.githubusercontent.com/u/115578812?v=4"
          alt=""  
        />
      </div>
      <div className={`${styles.profile_banner_text} `} >
        <h2 className={`${styles.neontext} `}>
            Hi 
          👋
            I'm
        </h2>
       <h1 id="user-detail-name" className={`${styles.neonText} `}>
        Rohit Banerjee
       </h1>



       <Typed
              style={{fontSize:"22px",fontWeight:"600"}}   strings={[
                      'A Software Developer',"A Problem Solver",
                      "A Mern Stack Developer",
                    "And a Musician"]}
                    typeSpeed={40}
                    backSpeed={50}
          
                    loop
                />
      </div>
    </div>
  );
};

export default Profilebanner;
