import React, { useEffect } from "react";
import styles from "./Education.module.css";
import AOS from 'aos';
import 'aos/dist/aos.css';
const Education = () => {
  useEffect(() => {
    AOS.init();
  }, []);
  return (
    <div className={`${styles.Education_Page}`} data-aos="fade-in" data-aos-duration="3000">
      <h1 >Education</h1>
      <div className={`${styles.Education_cards_component}`}>
        <div className={`${styles.edu_card} ${styles.card_1}` }data-aos="fade-left">
          <h2>Full Stack Web Development</h2>
          <div>
            <p className={`${styles.edu_card_p}`}>
              
              Currently pursuing
              <span style={{ color: "#8EDF52", fontWeight: "600" }}>
              
                ( 2021-present )
              </span>
            </p>
          </div>
          <h3>Masai School (Bengaluru)</h3>
        </div>

        <div className={`${styles.edu_card} ${styles.card_2}`} data-aos="fade-right">
          <h2>Industrial Fisheries( B.Sc )</h2>
          <div>
            <p className={`${styles.edu_card_p}`}>
              {" "}
              Currently pursuing{" "}
              <span style={{ color: "#15d2d9", fontWeight: "600" }}>
                
                ( 2019-2021 )
              </span>
            </p>
            <h3>
            Asutosh College( Calcutta University )
            </h3>
          </div>
          <h3></h3>
        </div>

        <div className={`${styles.edu_card} ${styles.card_3}`} data-aos="fade-left">
          <h2>Higher Secondary</h2>
          <div>
            <p className={`${styles.edu_card_p}`}>
              {" "}
             {" "}
              <span style={{ color: "#15d2d9", fontWeight: "600" }}>
                {" "}
                (  2017-2019 )
              </span>{" "}
            </p>
          </div>
          <h3>Harinavi D.V.A.S. High School</h3>
        </div>

      </div>
    </div>
  );
};

export default Education;
