import React from "react";
import styles from "./About.module.css";
import resume from "../Github/Rohit-Banerjee-Resume.pdf"
const Qualification = () => {


  const handleDownload = () => {
    // Replace 'file_path_or_url' with the path or URL of the file you want to download
    const fileUrl = resume;
  
    // Trigger the background download
    const link = document.createElement("a");
    link.href = fileUrl;
    link.setAttribute("download", "Resume.pdf"); // Set the desired file name and extension
    link.style.display = "none";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  
    // Open the resume in a new tab
    window.open(fileUrl, "_blank");
  };
  
  return (
    <div className={`${styles.Qualifications}`}>
      <h2
        style={{
          color: "rgb(255, 229, 58)",
          fontWeight: "600",
        }}
      >
        Personal Details
      </h2>

      <p>
        Name : Rohit Banerjee
        <br />
        Gender : Male
        <br />
        Date of Birth : 20/03/2002
        <br />
        Location : Kolkata ( South 24 pgs )
      </p>

      <h3
        style={{
          color: "rgb(255, 229, 58)",
          fontSize: "25px",
          fontWeight: "100",
        }}
      >
        Contact details
      </h3>
      <p>
      <p id="contact-email">  Email : rohitbanerjee202@gmail.com</p>
     <p  id="contact-linkedin">     Linkdin : Rohit Banerjee</p>
   
     <p  id="contact-github">     Github : Rohit1438</p>
   <p  id="contact-phone">Phone: 8420822373</p>
      </p>
      <h3
        style={{
          color: "rgb(255, 229, 58)",
          fontSize: "25px",
          fontWeight: "100",
        }}
      >
        Languages known
      </h3>
      <p>Bengali (M)
        <br />
      English
      <br />
      Hindi</p>


      <div>
<button  id="resume-button-2" className={`${styles.resumeBtn} nav-link resume`} onClick={handleDownload}>resume</button></div>
    

    </div>
  );
};

export default Qualification;
