import React from 'react';
import GitHubContributions from './Calendar';
import styled from '@emotion/styled';

const Github = () => {
  return (
    <Div>
      <div style={{ display: "flex", alignItems: "center", flexDirection: "column" }}>
        <h1>GitHub Streak</h1>
        <div className='streak' data-aos="fade-up" data-aos-duration="700">
          <iframe
            className='card'
            title="GitHub Streak"
            id="github-streak-stats"
            src="https://github-readme-streak-stats.herokuapp.com?user=Rohit1438"
     
          />
        </div>
        <div className='stats' >
          <iframe data-aos="fade-right" data-aos-duration="700"
            className='card'
            title="GitHub Stats"
            id="github-stats-card"
            src="https://github-readme-stats.vercel.app/api?username=Rohit1438"
          
          />
          <iframe data-aos="fade-left" data-aos-duration="700"
            className='card'
            title="GitHub Top Langs"
            id="github-top-langs"
            src="https://github-readme-stats.vercel.app/api/top-langs?username=Rohit1438"
        
          />
        </div>
        <div className='calenderdiv'>
 <div className='calender'>
 <GitHubContributions />
 </div>
        </div>
      </div>
    </Div>
  );
};

export default Github;

const Div = styled.div`
margin-top: 50px;
  .streak {
    width: 100%;
    
        height: 300px;
    display: flex;
    justify-content: space-between;
  }
.streak>iframe{
  width: 95%;
  /* height: 100%; */
  
  
  border:none;
 
}
.stats>iframe{
  width: 80%;
  margin: auto;
  border:none;
}
  .stats {
    width: 90%;
    height: 354px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 50px;
  }

  .card {

  }

.card {
    height: 100%;
    outline:none;
  
  }

  .calenderdiv {
    width: 100%;
    height: 100%;
    padding: 50px 0px 50px;
    margin: auto;
    display: flex;
    color: white;
    background-color: #272727;
  }
.calender{
  width: 80%;
    height: 100%;
    padding: 50px 0px 50px;
    margin: auto;
    display: flex;
    color: white;
    background-color: #272727;
}
  @media (max-width: 600px) {
    width: 100%;
    .stats {margin-top:0px;
      display: flex;
      flex-direction: column;
    }
    .calender{
  width: 100%;
    height: 100%;
 
    margin: auto;
    display: flex;
    color: white;
    background-color: #272727;
}



.calender{
  width: 80%;
    height: 100%;
    padding: 0px 0px 0px;
    margin: auto;
    display: flex;
    color: white;

}
.streak {
    width: 100%;
    
        height: 100px;
    display: flex;
    justify-content: space-between;
  }
.streak>iframe{
  /* width: 80%; */
  height: 100px;
  padding: 0px;
  margin: auto;
  border:none;
 
}
.stats>iframe{
  width: 80%;
  margin: auto;
  border:none;
}
 


  }
`;
